
import Vue from 'vue';
import { mapGetters, mapActions } from 'vuex';
import UserItem from '../components/UserItem.vue';

export default Vue.extend({
  name: 'Users',
  components: {
    UserItem,
  },
  computed: mapGetters(['allUsers']),
  methods: mapActions(['fetchUsers']),
  async mounted() {
    this.fetchUsers();
  },
});
